.package-home {
  padding: 0 5%;
  overflow-x: hidden;
}

.box {
  background-color: #f3ece7;
  margin: 10% auto;
  border-radius: 10px;
  width: 80%;
  height: 80%;
  padding: 2%;
  overflow-y: hidden;
}

.button--pack {
  font-size: large;
  font-weight: bold;
  margin: 5%;
  background-color: #e4dac2;
  text-transform: uppercase;
  white-space: normal;
  word-break: break-word;
}

.desc--text {
  margin: 5%;
}

.package__img__box {
  margin-top: 5%;
  width: 70%;
}
