@import url("https://fonts.googleapis.com/css?family=Quicksand:400,500,700");

.set-screen-pack {
  background: url("/img/cart/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 1% 0 5% 0;
  min-height: calc(100vh - 155px);
  background-attachment: fixed;
  background-position: top left;
}

.set-frame-pks {
  margin: 70px auto 0 auto;
  padding: 0 !important;
  width: 65%;
}

.set-frame-each-pks {
  padding: 0 2% 0 2%;
}

.set-each-package {
  margin: 30px auto 30px auto;
  background-color: #f3ece7;
  color: #7b7b7b;
  padding: 3%;
  border-radius: 12px;
}

.des-box {
  white-space: normal;
  word-break: break-word;
}

.name-each-pks {
  font-weight: bold;
  margin: 0 auto 15px auto;
  font-size: 18px;
  border-radius: 50px;
  padding: 8px;
}

.img-pack {
  width: 80%;
  border-radius: 10px;
  margin: 10px 0 10px 0;
}

.nav-setting {
  width: 154px;
  margin-top: 30px;
  background-color: #e5e4e2;
  border-radius: 12px 12px 0 0;
  border: none;
  color: #7b7b7b;
}

.tab-setting {
  border-radius: 12px 12px 0 0;
}

a.active {
  background: #f3ece7 !important;
  border-radius: 12px 12px 0 0;
  border: 0ch;
}

.backtopks {
  margin: 70px 0 0 1%;
  font-weight: bold;
}

.package-box {
  margin: 20px 1.5% 70px 1.5%;
  background-color: #f3ece7;
  border-radius: 12px;
  font-family: "Quicksand", sans-serif;
  font-size: 17px;
  color: #7b7b7b;
