@import url("https://fonts.googleapis.com/css?family=Quicksand:400,500,700");

.setbg__addmenu {
  background: url("/img/register/bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: auto;
}

.addmenu__box {
  background-color: #f3ece7;
  border-radius: 30px;
  padding-top: 40px;
  padding-right: 50px;
  padding-left: 50px;
  width: 70%;
  height: auto;
  max-width: 450px;
  display: inline-block;
  font-family: "Quicksand", sans-serif;
  margin-top: 70px;
  margin-bottom: 30px;
}

.submit__addmenu--button {
  margin: 5% 0 10% 0;
  background-color: #ccc0a5;
  color: white;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  padding: 5px;
  width: 90%;
  min-width: 100px;
  font-family: "Quicksand", sans-serif;
  height: auto;
}

.imgpreview {
  width: 80%;
  border-radius: 6px;
  padding-bottom: 8px;
  padding-right: 10%;
}
